import React, { forwardRef } from "react";

import { Icon } from "../Icon/Icon";

import {
	ButtonText, IconWrapper, StyledButton,
} from "./Button.styles";
import { ButtonProps } from "./Button.types";

const Button = forwardRef<HTMLButtonElement, ButtonProps>((props, ref) => {
	const {
		dataQA,
		leftIcon,
		rightIcon,
		primaryButtonColor = "blue",
		size = "Small",
		buttonText,
	} = props;

	return (
		<StyledButton
			ref={ref}
			data-qa={dataQA}
			primaryButtonColor={primaryButtonColor}
			size={size}
			{...props}
		>
			{leftIcon
				&& (
					<IconWrapper data-qa={`${dataQA}-left-icon`}>
						<Icon name={leftIcon} size={20} />
					</IconWrapper>
				)}
			{buttonText
				&& (
					<ButtonText
						dataQA="button-text"
						variant="bodyCopyBold"
						component="span"
						color="inherit"
					>
						{buttonText}
					</ButtonText>
				)}
			{rightIcon
				&& (
					<IconWrapper data-qa={`${dataQA}-right-icon`}>
						<Icon name={rightIcon} size={20} />
					</IconWrapper>
				)}
		</StyledButton>
	);
});
Button.displayName = "Button";
export default Button;
